.container {
  position: relative;
  margin: 0 auto;

  width: 100%;
  height: 100vh;
}

body{
  font-family: 'Roboto', cursive;
  margin:0
}

h1 {
  text-align: center;
  font-family: 'Black Ops One', cursive;
  font-weight: 300;
  margin-bottom: 0;


}
@media only screen and (max-width: 600px){
  h1{
    font-size:30px;
  }
}



h2{
  font-family: 'Black Ops One', cursive;
  font-weight: 300;
  margin-bottom: 0;
  margin-top: 0;
  color:black;
}

p{
  font-family: 'Open Sans', cursive;
}


.button{
  background-color: rgb(69, 171, 194);
  font-size: 20px;
  font-weight: 900;
  margin-top: 30px;
  border:3px;
  border-style:outset;
  border-color: rgb(229, 246, 250);
  border-radius: 10px;
  padding:5px 15px;


  font-family: 'Black Ops One', cursive;
  color: #06172a
}





/*BOARD*/



.board {
  background-image: url("./svg/background/space.jpg");
  background-size: cover;
  position: absolute;
  overflow: hidden;
  user-select: none;
  /*height: 400px;*/

  display:flex;
  justify-content: center;
  align-items: center;


  width: 100%;
  height: 100%;
  background-color: aquamarine;
}


.loading {
  position: absolute ;
 display:flex;
  justify-content: center;
  align-items: center;
  height:60%;

  padding:30px;
  user-select: none;

}


.boardStartDialogContainer {
  position: absolute ;
  display:flex;
  justify-content: center;
  align-items: center;
  height:100%;
width:100%;
  margin:0 auto;
  user-select: none;

}



.boardStartDialog {
  display:flex;
  justify-content: center;
  align-content: center;
  flex-direction: column;
  width:60%;
  height:max-content;

padding:30px;

  background-color: rgba(255, 255, 255, 0.5);
  border-radius: 20px;
  box-shadow: 0px 20px 60px rgba(0, 0, 0, 0.5);

}

@media only screen and (max-width: 600px){
  .boardStartDialog{
  margin:8px;
    width:90%;

  }
}

.cursor{
  width: 200px;
  height: 200px;
  background-image: url("./svg/hammer.jpg");
  background-size: cover;
/*  background-color:rgba(250, 128, 114, 0.774);*/
  position: absolute;

  z-index: 999;
  pointer-events: none;

}
.animation{
  transform: rotate(-30deg)
}

